@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap);
body{margin:0;padding:0}.app-container{display:flex;justify-content:center;align-items:center;height:100vh;background-color:#181818;font-family:'Roboto'}.app-container .chat-container{height:80%;border-radius:20px;padding:16px;background-color:white}.app-container .chat-container .messages-container{height:90%;overflow-y:auto}.app-container .chat-container .messages-container .messages{display:flex;flex-direction:column}.app-container .chat-container .send-msg-container{display:flex;align-items:flex-end;height:10%}.app-container .chat-container .send-msg-container .text-input-container{display:flex;flex-grow:1}.app-container .chat-container .send-msg-container .text-input-container input{width:100%;height:40px;border:none;border-radius:50px;outline:none;padding:0px 16px;background-color:#eeeeee}.app-container .chat-container .send-msg-container .send-icon-container{display:flex;justify-content:flex-end}.app-container .chat-container .send-msg-container .send-icon-container .send-icon{display:flex;justify-content:center;align-items:center;width:40px;height:40px;border:none;border-radius:50px;margin-left:6px;cursor:pointer;background-color:#25D366}.app-container .chat-container .send-msg-container .send-icon-container .send-icon i{color:white}

.login-container form{display:flex;flex-direction:column;align-items:center;font-size:16px}.login-container form label{color:white;font-weight:600}.login-container form input{margin:8px 0px 16px 0px;padding:10px 16px;border:none;outline:none;border-radius:20px}.login-container form button{width:100%;border:none;padding:10px 0px;border-radius:20px;cursor:pointer;background-color:#25D366;color:white;font-weight:600;transition:all 1s;font-size:inherit}.login-container form button:hover{background-color:#28e76f}

.message-container{max-width:80%;overflow-x:hidden;overflow-wrap:break-word;display:flex;flex-direction:column;align-self:flex-start;margin:0px 0px 16px 0px}.message-container.self-sender-container{align-self:flex-end}.message-container.self-sender-container .message-time{padding-right:14px;text-align:right}.message-container .message{display:inline-block;padding:10px 14px;border-radius:16px;background-color:#eeeeee;color:#181818;font-size:14px}.message-container .message.self-sender{background-color:#25D366;color:white}.message-container .message-sender{margin-top:4px;font-size:10px;padding-left:14px}

